import React from 'react'
import { connect } from 'react-redux'
import { Dialog, DataTable } from '@tencent/bere'
import { jobsOperation } from '@constant/jobs'

const columns = [
  {
    key: 'index',
    dataIndex: 'index',
    title: '排序'
  },
  {
    key: 'name',
    dataIndex: 'name',
    title: '项目名称'
  },
  {
    key: 'id',
    dataIndex: 'id',
    title: '项目id'
  }
]

class JobsDialog extends React.Component {
  state = { expand: true }

  handleOK = () => {
    const { action } = this.props

    this.props.dispatch({ type: 'jobs/closeDialog' })
    this.props.dispatch(action)
  }

  handleCancel = () => {
    this.props.dispatch({ type: 'jobs/closeDialog' })
  }

  handleExpand = e => {
    e.preventDefault()
    this.setState({
      expand: !this.state.expand
    })
  }

  render() {
    const { visible, operation, batch, selectedJobs, jobsList } = this.props
    const { expand } = this.state
    const { name, confirmMessage } = jobsOperation[operation] || {
      name: '',
      confirmMessage: ''
    }
    const data = selectedJobs.map((job, i) => {
      return { ...job, index: i + 1 }
    })

    return (
      <Dialog
        title={`确定${name}所选项目？`}
        visible={visible}
        okText={name}
        cancelText="取消"
        onOk={this.handleOK}
        onCancel={this.handleCancel}
      >
        {batch ? (
          <React.Fragment>
            <p className="text mb-10">
              您已选
              <span className="text-danger">{` ${selectedJobs.length} `}</span>
              个项目
              <a href="javascript:;" onClick={this.handleExpand}>
                查看详情
                <i className={`icon-arrow-${expand ? 'up' : 'down'}`} />
              </a>
            </p>
            {expand ? (
              <DataTable
                columns={columns}
                data={data}
                hasHead={false}
                scroll={{ y: 330 }}
              />
            ) : (
              ''
            )}
          </React.Fragment>
        ) : (
          ''
        )}
        {confirmMessage}
      </Dialog>
    )
  }
}

export default connect(({ jobs }) => {
  return {
    selectedJobs: jobs.selectedJobs,
    jobsList: jobs.jobsList,
    ...jobs.dialog
  }
})(JobsDialog)
