import React from 'react'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import { Button } from '@tencent/bere'

import { JobsDialog, JobsTable, JobsPager, JobsSearch } from './JobsTable'

class JobsList extends React.Component {
  componentDidMount() {
    this.props.dispatch({ type: 'jobs/closeDialog' })
    this.props.dispatch({ type: 'jobs/refresh' })

    this.props.history.listen(() => {
      this.props.dispatch({ type: 'jobs/initialize' })
    })
  }

  handleOperate = e => {
    e.preventDefault()
    e.stopPropagation()
    const { selectedJobs } = this.props
    const { operation } = e.currentTarget.dataset
    const id = selectedJobs.map(job => job.id)
    this.props.dispatch({
      type: `jobs/openDialog`,
      payload: {
        action: {
          type: `jobs/${operation}Jobs`,
          payload: { id }
        },
        operation,
        batch: true
      }
    })
  }

  render() {
    const { selectedJobs } = this.props
    return (
      <div className="manage-area">
        <JobsDialog />
        <div className="manage-area-title">
          <h2>检测项目管理</h2>
        </div>
        <div className="manage-area-main">
          <div className="manage-area-main-inner">
            <div className="tc-action-grid">
              <div className="justify-grid">
                <div className="col">
                  <Link to="/jobs/edit/new" title="新建检测项目">
                    <Button className="m btn-add">新建</Button>
                  </Link>
                  <Button
                    className="m weak"
                    disabled={!selectedJobs[0]}
                    onClick={this.handleOperate}
                    data-operation="delete"
                  >
                    删除
                  </Button>
                  <Button
                    className="m weak"
                    disabled={!selectedJobs.some(job => job.status === 'STOP')}
                    onClick={this.handleOperate}
                    data-operation="start"
                  >
                    启用
                  </Button>
                  <Button
                    className="m weak"
                    disabled={!selectedJobs.some(job => job.status === 'START')}
                    onClick={this.handleOperate}
                    data-operation="stop"
                  >
                    停用
                  </Button>
                </div>
                <div className="col">
                  <JobsSearch />
                </div>
              </div>
            </div>
            <div className="tc-panel panel-table">
              <JobsTable />
              <JobsPager />
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default connect(({ jobs }) => {
  return {
    selectedJobs: jobs.selectedJobs
  }
})(JobsList)
