import React from 'react'
import { connect } from 'react-redux'
import { RadioButtonGroup } from '@tencent/bere'
import { DateRangePicker } from '@components/DateTime'

class Schedule extends React.Component {
  handleScheduleChange = schedule => {
    this.props.dispatch({
      type: 'jobs/change',
      payload: { schedule }
    })
  }

  handleScheduleTimeChange = ({ from, to }) => {
    this.props.dispatch({
      type: 'jobs/change',
      payload: {
        scheduleStartTime: from.format('YYYY-MM-DD hh:mm:ss'),
        scheduleEndTime: to.format('YYYY-MM-DD hh:mm:ss')
      }
    })
  }

  render() {
    const { schedule } = this.props
    let { scheduleStartTime, scheduleEndTime } = this.props
    if (!scheduleStartTime) {
      scheduleStartTime = scheduleEndTime = new Date()
    }
    return (
      <li>
        <div className="form-label">
          <label>布控时间</label>
        </div>
        <div className="form-input">
          <div className="form-unit">
            <RadioButtonGroup
              value={schedule}
              list={[
                { text: '永久', value: 0 }
                // { text: '固定日期', value: 1 }
              ]}
              onChange={this.handleScheduleChange}
            />
            {schedule === 0 ? (
              ''
            ) : (
              <DateRangePicker
                value={{ from: scheduleStartTime, to: scheduleEndTime }}
                isUntilNow={false}
                onChange={this.handleScheduleTimeChange}
              />
            )}
          </div>
        </div>
      </li>
    )
  }
}

export default connect(({ jobs }) => {
  return {
    schedule: jobs.schedule,
    scheduleStartTime: jobs.scheduleStartTime,
    scheduleEndTime: jobs.scheduleEndTime
  }
})(Schedule)
