import React from 'react'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import { FormGroupList, Button } from '@tencent/bere'

import {
  NameControl,
  ScheduleControl,
  TemplateControl,
  SiteControl,
  ProductsControl,
  SubmitControl
} from './JobsEditorControl'

class JobsEditor extends React.Component {
  componentDidMount() {
    this.props.dispatch({ type: 'jobs/closeDialog' })

    const id = this.props.match.params.id
    if (id === 'new') {
      this.props.dispatch({ type: 'jobs/initializeNewJob' })
    } else {
      this.props.dispatch({
        type: 'jobs/fetchJob',
        payload: { id }
      })
      this.props.dispatch({
        type: 'jobs/change',
        payload: { id, editMode: 'update' }
      })
    }
  }

  render() {
    const id = this.props.match.params.id
    return (
      <div className="manage-area">
        <div className="manage-area-title secondary-title">
          <Link to="/jobs" className="back-link" title="检测项目管理">
            <i className="btn-back-icon" />
            返回
          </Link>
          <span className="line-icon">|</span>
          <h2>{id === 'new' ? '新建项目' : '编辑任务'}</h2>
        </div>
        <div className="manage-area-main secondary-main">
          <div className="manage-area-main-inner">
            <div className="tc-panel">
              <div className="tc-panel-bd">
                <div className="param-box">
                  <div className="param-hd">
                    <h3>基本信息</h3>
                  </div>
                  <div className="param-bd">
                    <FormGroupList>
                      <NameControl />
                      <ScheduleControl />
                      <TemplateControl />
                    </FormGroupList>
                  </div>
                </div>
                <div className="param-box">
                  <div className="param-hd">
                    <h3>应用站点产品</h3>
                  </div>
                  <div className="param-bd">
                    <FormGroupList>
                      <li>
                        <div className="form-input">
                          <div className="form-unit">
                            <div className="tc-15-mod-selector">
                              <SiteControl />
                              <ProductsControl />
                              <div className="tc-15-mod-selector-tips">
                                支持按住shift键进行多选
                              </div>
                            </div>
                          </div>
                        </div>
                      </li>
                    </FormGroupList>
                  </div>
                </div>
              </div>
              <div className="tc-panel-ft">
                <SubmitControl />
                <Link to="/jobs" title="检测项目管理">
                  <Button className="tc-15-btn weak">取消</Button>
                </Link>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default connect(({ jobs }) => {
  return {
    name: jobs.name,
    scheduleStartTime: jobs.scheduleStartTime,
    scheduleEndTime: jobs.scheduleEndTime,
    schedule: jobs.schedule,
    templateId: jobs.templateId,
    siteMap: jobs.siteMap,
    site: jobs.site,
    shop: jobs.shop,
    operation: jobs.operation,
    products: jobs.products
  }
})(JobsEditor)
