import React from 'react'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import { DataTable } from '@tencent/bere'
import { jobsStatus } from '@constant/jobs'

const columns = [
  {
    type: 'selection',
    key: '_selection',
    dataIndex: 'id',
    width: 36
  },
  {
    key: 'name',
    dataIndex: 'name',
    title: '项目名称'
  },
  {
    key: 'id',
    dataIndex: 'id',
    title: '项目ID',
    width: 250
  },
  {
    key: 'template',
    dataIndex: 'template',
    title: '服务算法'
  },
  {
    key: 'config',
    dataIndex: 'config',
    title: '应用站点产品',
    render: (value, record) => {
      const { products, site, shop, operation } = record.config

      return (
        <div>
          <div className="tc-15-bubble-icon tc-15-triangle-align-start">
            <span className="site-manage-more-list">
              <a href="javascript:;">{products.length}个</a>
            </span>
            <div className="tc-15-bubble tc-15-bubble-top">
              <div className="tc-15-bubble-inner">
                {products.map(({ product }) => (
                  <p
                    key={product}
                  >{`${site}/${shop}/${operation}/${product}`}</p>
                ))}
              </div>
            </div>
          </div>
        </div>
      )
    }
  },
  {
    key: 'status',
    dataIndex: 'status',
    title: '状态',
    render: value => (
      <div>
        <span className={`text-${value === 'START' ? 'success' : 'danger'}`}>
          {jobsStatus[value]}
        </span>
      </div>
    ),
    width: 150
  },
  {
    key: 'cTime',
    dataIndex: 'cTime',
    title: '创建时间',
    sortable: true,
    width: 150
  }
]

class JobsTable extends React.Component {
  handleSelectionChange = (key, record, checked) => {
    const { selectedJobs, jobsList } = this.props
    if (checked) {
      const target = jobsList.find(job => job.id === key)
      this.props.dispatch({
        type: 'jobs/change',
        payload: {
          selectedJobs: [...selectedJobs, target]
        }
      })
    } else {
      this.props.dispatch({
        type: 'jobs/change',
        payload: {
          selectedJobs: selectedJobs.filter(selected => selected.id !== key)
        }
      })
    }
  }

  handleSelectionAllChange = checked => {
    const { jobsList } = this.props

    this.props.dispatch({
      type: 'jobs/change',
      payload: {
        selectedJobs: checked ? [...jobsList] : []
      }
    })
  }

  handleSort = () => {
    this.props.dispatch({ type: 'jobs/changeSort' })
  }

  handleOperate = e => {
    e.preventDefault()
    e.stopPropagation()
    const id = e.currentTarget.parentNode.dataset.key
    const { operation } = e.currentTarget.dataset
    this.props.dispatch({
      type: `jobs/openDialog`,
      payload: {
        action: {
          type: `jobs/${operation}Job`,
          payload: { id }
        },
        operation,
        batch: false
      }
    })
  }

  get columns() {
    return [
      ...columns,
      {
        key: '',
        dataIndex: '',
        title: '操作',
        render: (value, record) => (
          <div key={record.id} data-key={record.id}>
            {record.status === 'START' ? (
              <a
                onClick={this.handleOperate}
                data-operation="stop"
                href="javascript:;"
              >
                停用
              </a>
            ) : (
              <a
                onClick={this.handleOperate}
                data-operation="start"
                href="javascript:;"
              >
                启用
              </a>
            )}

            <Link to={`jobs/edit/${record.id}`} title="检测项目修改">
              修改
            </Link>
            <a
              onClick={this.handleOperate}
              data-operation="delete"
              href="javascript:;"
            >
              删除
            </a>
          </div>
        )
      }
    ]
  }

  render() {
    const {
      isJobsLoading,
      jobsList,
      selectedJobs,
      templateList,
      sortOrder
    } = this.props

    const data = jobsList.map(job => {
      const { templateId } = job
      const template = templateList.find(item => templateId === item.id)
      return {
        ...job,
        template: template ? template.name : templateId
      }
    })

    return (
      <DataTable
        columns={this.columns}
        loading={isJobsLoading}
        data={data}
        rowKey="id"
        sortKey="cTime"
        sortOrder={sortOrder}
        onSort={this.handleSort}
        selection={{
          value: selectedJobs.map(job => job.id),
          mode: 'row',
          onChange: this.handleSelectionChange,
          onChangeAll: this.handleSelectionAllChange
        }}
      />
    )
  }
}

export default connect(({ jobs }) => {
  return {
    selectedJobs: jobs.selectedJobs,
    jobsList: jobs.jobsList,
    total: jobs.total,
    limit: jobs.limit,
    page: jobs.page,
    isJobsLoading: jobs.isJobsLoading,
    sortOrder: jobs.sortOrder,
    searchText: jobs.searchText,
    templateList: jobs.templateList
  }
})(JobsTable)
