import React from 'react'
import { connect } from 'react-redux'
import { Select } from '@tencent/bere'

class Template extends React.Component {
  handleChange = templateId => {
    this.props.dispatch({
      type: 'jobs/change',
      payload: { templateId }
    })
  }

  render() {
    const { templateId, templateList } = this.props
    return (
      <li>
        <div className="form-label">
          <label>服务算法</label>
        </div>
        <div className="form-input">
          <div className="form-unit">
            <Select
              value={templateId}
              list={templateList.map(({ id, name }) => ({
                value: id,
                text: name
              }))}
              selectFirstByDefault={false}
              placeholder="请选择服务算法"
              className="tc-15-select xl"
              onChange={this.handleChange}
            />
          </div>
        </div>
      </li>
    )
  }
}

export default connect(({ jobs }) => {
  return {
    templateId: jobs.templateId,
    templateList: jobs.templateList
  }
})(Template)
