import React from 'react'
import { connect } from 'react-redux'
import { Input } from '@tencent/bere'
import classnames from 'classnames'

class Name extends React.Component {
  handleChange = e => {
    this.props.dispatch({
      type: 'jobs/change',
      payload: { name: e.target.value }
    })
  }

  render() {
    const { name, nameError } = this.props
    return (
      <li>
        <div className="form-label">
          <label>项目名称</label>
        </div>
        <div
          className={classnames({ 'form-input': true, 'is-error': nameError })}
        >
          <div className="form-unit">
            <Input value={name} onChange={this.handleChange} />
            <p className="form-input-help">
              仅支持中文、字母、数字和"_"的组合，不超过20个字
            </p>
          </div>
        </div>
      </li>
    )
  }
}

export default connect(({ jobs }) => {
  return {
    name: jobs.name,
    nameError: jobs.nameError
  }
})(Name)
