import React from 'react'
import { connect } from 'react-redux'
import { Switch, Route } from 'react-router-dom'

import JobsList from './JobsList'
import JobsEditor from './JobsEditor'

class App extends React.Component {
  componentDidMount() {
    const { templateList } = this.props
    if (!templateList[0]) {
      this.props.dispatch({ type: 'jobs/fetchTemplates' })
    }
    this.props.dispatch({ type: 'jobs/closeDialog' })
  }

  render() {
    return (
      <div className="main adc-manage-main">
        <Switch>
          <Route path="/jobs/edit/:id" excat component={JobsEditor} />
          <Route path="/jobs" component={JobsList} />
        </Switch>
      </div>
    )
  }
}

export default connect(({ jobs }) => {
  return { templateList: jobs.templateList }
})(App)
