import React from 'react'
import { connect } from 'react-redux'
import { Select } from '@tencent/bere'

const ascSort = (a, b) => (a > b ? 1 : -1)
const descSort = (a, b) => (b > a ? 1 : -1)

class Site extends React.Component {
  componentDidMount() {
    this.props.dispatch({ type: 'jobs/fetchSiteMap' })
  }

  handleSiteChange = site => {
    this.props.dispatch({
      type: 'jobs/change',
      payload: { site, shop: '', operation: '', productList: [], products: [] }
    })
  }

  handleShopChange = shop => {
    this.props.dispatch({
      type: 'jobs/change',
      payload: { shop, operation: '', productList: [], products: [] }
    })
  }

  handleOperationChange = operation => {
    const { site, shop } = this.props
    this.props.dispatch({
      type: 'jobs/change',
      payload: { operation, productList: [], products: [] }
    })

    this.props.dispatch({
      type: 'jobs/fetchProducts',
      payload: { site, shop, operation }
    })
  }

  render() {
    const { siteMap, site, shop, operation, editMode } = this.props
    let curSite
    let shopList
    let operationList
    const siteList = siteMap
      .map(item => {
        if (item.site === site) {
          curSite = item
        }
        return { text: item.site, value: item.site }
      })
      .sort((a, b) => ascSort(a.text, b.text))

    if (curSite && curSite.factory_list) {
      let curShop
      shopList = curSite.factory_list
        .map(item => {
          if (item.factory_name === shop) {
            curShop = item
          }
          return { text: item.factory_name, value: item.factory_name }
        })
        .sort((a, b) => ascSort(a.text, b.text))

      if (curShop && curShop.pon_list) {
        operationList = curShop.pon_list
          .map(item => ({
            text: item.process_operation_name,
            value: item.process_operation_name
          }))
          .sort((a, b) => ascSort(a.text, b.text))
      }
    }

    return (
      <div className="tc-15-mod-selector-area">
        <h4 className="run-in">产品分类</h4>
        <Select
          className="tc-15-select s mr-10"
          value={site}
          list={siteList}
          selectFirstByDefault={false}
          placeholder="请选择厂区"
          onChange={this.handleSiteChange}
          disabled={editMode !== 'new'}
        />
        <Select
          className="tc-15-select s mr-10"
          value={shop}
          list={shopList}
          selectFirstByDefault={false}
          placeholder="请选择厂别"
          onChange={this.handleShopChange}
          disabled={!site}
        />
        <Select
          className="tc-15-select s"
          value={operation}
          list={operationList}
          selectFirstByDefault={false}
          placeholder="请选择站点"
          onChange={this.handleOperationChange}
          disabled={!shop}
        />
      </div>
    )
  }
}

export default connect(({ jobs }) => {
  return {
    siteMap: jobs.siteMap,
    site: jobs.site,
    shop: jobs.shop,
    operation: jobs.operation,
    products: jobs.products,
    productList: jobs.productList,
    editMode: jobs.editMode
  }
})(Site)
