import React from 'react'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import { Button, TopTips, Dialog } from '@tencent/bere'
import validator from './validator'

function validate(data) {
  for (const field in data) {
    if (!validator[field]) {
      continue
    }

    const error = validator[field](data[field], data)
    if (error) {
      return {
        message: error,
        field,
        value: data[field]
      }
    }
  }
  return null
}

class Submit extends React.Component {
  handleSubmit = e => {
    const {
      editMode,
      isSubmitting,
      id,
      name,
      scheduleStartTime,
      scheduleEndTime,
      schedule,
      templateId,
      site,
      shop,
      operation,
      products,
      monitor,
      reply,
      status
    } = this.props

    if (isSubmitting) {
      return
    }

    const data = {
      name,
      scheduleStartTime,
      scheduleEndTime,
      schedule,
      templateId,
      site,
      shop,
      operation,
      products,
      monitor,
      reply,
      status
    }

    const error = validate(data)

    if (error) {
      this.props.dispatch({
        type: 'jobs/change',
        payload: { nameError: error.field === 'name' }
      })

      return TopTips({ theme: 'error', message: error.message })
    }

    this.props.dispatch({ type: 'jobs/startSubmit' })
    if (editMode === 'new') {
      this.props.dispatch({
        type: 'jobs/addJob',
        payload: data
      })
    } else {
      this.props.dispatch({
        type: 'jobs/updateJob',
        payload: { id, ...data }
      })
    }
  }

  handleCancel = e => {
    this.props.dispatch({ type: 'jobs/closeDialog' })
  }

  render() {
    const { showMessageDialog } = this.props

    return (
      <React.Fragment>
        <Button onClick={this.handleSubmit}>确定</Button>
        <Dialog
          visible={showMessageDialog}
          okText="取消"
          hasCancel={false}
          onOk={this.handleCancel}
          onCancel={this.handleCancel}
          title="缺少消息配置"
        >
          该厂区缺少消息配置，请先在
          <Link to="/product/message">消息配置</Link>
          中配置消息
        </Dialog>
      </React.Fragment>
    )
  }
}

export default connect(({ jobs }) => {
  return {
    id: jobs.id,
    editMode: jobs.editMode,
    name: jobs.name,
    scheduleStartTime: jobs.scheduleStartTime,
    scheduleEndTime: jobs.scheduleEndTime,
    schedule: jobs.schedule,
    templateId: jobs.templateId,
    siteMap: jobs.siteMap,
    site: jobs.site,
    shop: jobs.shop,
    operation: jobs.operation,
    products: jobs.products,
    monitor: jobs.monitor,
    reply: jobs.reply,
    status: jobs.status,
    showMessageDialog: jobs.showMessageDialog,
    isSubmitting: jobs.isSubmitting
  }
})(Submit)
