import React from 'react'
import { connect } from 'react-redux'
import { Pager } from '@tencent/bere'

class JobsPager extends React.Component {
  handlePageChange = page => {
    this.props.dispatch({
      type: 'jobs/change',
      payload: { page }
    })

    this.props.dispatch({ type: 'jobs/refresh' })
  }

  handlePerPageChange = limit => {
    this.props.dispatch({
      type: 'jobs/change',
      payload: { limit, page: 0 }
    })

    this.props.dispatch({ type: 'jobs/refresh' })
  }

  render() {
    const { selectedJobs, total, page, limit } = this.props
    return (
      <Pager
        total={total}
        selected={selectedJobs.length}
        current={page}
        perPageCountList={[15, 30, 45]}
        perPageCount={limit}
        onPageChange={this.handlePageChange}
        onPerPageChange={this.handlePerPageChange}
      />
    )
  }
}

export default connect(({ jobs }) => {
  return {
    selectedJobs: jobs.selectedJobs,
    page: jobs.page,
    limit: jobs.limit,
    total: jobs.total,
    searchText: jobs.searchText
  }
})(JobsPager)
