import React from 'react'
import { connect } from 'react-redux'
import { SearchInput } from '@tencent/bere'

class JobsSearch extends React.Component {
  state = { searchText: '' }

  handleSearchTextChange = searchText => {
    this.setState({ searchText })
  }

  handleSearch = searchText => {
    const { page, limit } = this.props

    this.props.dispatch({
      type: 'jobs/change',
      payload: { searchText }
    })

    this.props.dispatch({
      type: 'jobs/fetchJobs',
      payload: { page, limit, searchText }
    })
  }

  render() {
    const { searchText } = this.state
    return (
      <SearchInput
        onChange={this.handleSearchTextChange}
        onSearch={this.handleSearch}
        value={searchText}
        placeholder="搜索名称/ID"
      />
    )
  }
}

export default connect(({ jobs }) => {
  return {
    searchText: jobs.searchText,
    page: jobs.page,
    limit: jobs.limit
  }
})(JobsSearch)
