const nameReg = /^[\u4e00-\u9fa5_a-zA-Z0-9]+$/

export default {
  name(value) {
    if (!value) {
      return '请填写项目名称'
    }

    if (!nameReg.test(value) || value.length > 20) {
      return '项目名称仅支持中文、字母、数字和"_"的组合，不超过20个字'
    }
  },

  templateId(value) {
    if (!value) {
      return '请选择服务算法'
    }
  },

  site(value) {
    if (!value) {
      return '请选择厂区'
    }
  },

  shop(value) {
    if (!value) {
      return '请选择厂别'
    }
  },

  operation(value) {
    if (!value) {
      return '请选择站点'
    }
  },

  products(value) {
    if (!value[0]) {
      return '请选择至少一项产品'
    }
  }
}
