import React from 'react'
import { connect } from 'react-redux'
import { SearchBox } from '@tencent/bere'

class Products extends React.Component {
  state = {
    startIndex: -1,
    searchValue: ''
  }

  handleCheck = e => {
    let index = parseInt(e.currentTarget.dataset.index)
    let { startIndex } = this.state
    if (e.shiftKey) {
      if (startIndex === -1) {
        this.setState({ startIndex: index })
      } else {
        const { products, productList } = this.props
        if (startIndex > index) {
          const temp = index
          index = startIndex
          startIndex = temp
        }
        this.props.dispatch({
          type: 'jobs/change',
          payload: {
            products: [...productList.slice(startIndex, index + 1), ...products]
          }
        })
        this.setState({ startIndex: -1 })
      }
      return
    }
    const { products, productList } = this.props
    this.props.dispatch({
      type: 'jobs/change',
      payload: {
        products: [productList[index], ...products]
      }
    })
    this.setState({ startIndex: -1 })
  }

  handleCancle = e => {
    const index = parseInt(e.currentTarget.dataset.index)
    const { products } = this.props
    const newProducts = [...products]
    newProducts.splice(index, 1)
    this.props.dispatch({
      type: 'jobs/change',
      payload: { products: newProducts }
    })
  }

  handleSearchChange = searchValue => {
    this.setState({ searchValue })
  }

  handleSearch = productSearch => {
    this.props.dispatch({
      type: 'jobs/change',
      payload: { productSearch }
    })
  }

  handleSearchClear = e => {
    this.setState({ searchValue: '' })
    this.props.dispatch({
      type: 'jobs/change',
      payload: { productSearch: '' }
    })
  }

  render() {
    const { productList, products } = this.props
    const { startIndex, searchValue } = this.state
    return (
      <div className="tc-15-mod-selector-tb">
        <div className="tc-15-option-cell options-left">
          <div className="tc-15-option-hd">
            <h4>选择产品</h4>
          </div>
          <div className="tc-15-option-bd">
            <SearchBox
              onChange={this.handleSearchChange}
              onSearch={this.handleSearch}
              onClear={this.handleSearchClear}
              value={searchValue}
            />
            <div className="tc-15-option-box tc-scroll">
              <ul className="tc-15-option-list">
                {productList.map(({ product }, index) => (
                  <li
                    key={product}
                    onClick={this.handleCheck}
                    data-index={index}
                  >
                    <input
                      type="checkbox"
                      id={`chkID${index}`}
                      checked={index === startIndex}
                      className="tc-15-checkbox"
                    />
                    <label htmlFor={`chkID${index}`} className="opt-txt">
                      <span className="opt-txt-inner">
                        <span className="item-name">{product}</span>
                      </span>
                    </label>
                  </li>
                ))}
              </ul>
            </div>
          </div>
        </div>
        <div className="tc-15-option-cell separator-cell">
          <i className="icon-sep" />
        </div>
        <div className="tc-15-option-cell options-right">
          <div className="tc-15-option-hd">
            <h4>
              已选择(
              {products.length})
            </h4>
          </div>
          <div className="tc-15-option-bd">
            <div className="tc-15-option-box tc-scroll">
              <ul className="tc-15-option-list">
                {products.map(({ product }, index) => (
                  <li key={product}>
                    <span className="opt-txt">
                      <span className="opt-txt-inner">
                        <span className="item-name">{product}</span>
                      </span>
                    </span>
                    <a
                      href="javascript:;"
                      role="button"
                      title="取消"
                      className="opt-act"
                      onClick={this.handleCancle}
                      data-index={index}
                    >
                      <i className="icon-del">取消</i>
                    </a>
                  </li>
                ))}
              </ul>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default connect(({ jobs }) => {
  return {
    products: jobs.products,
    productList: jobs.productList.filter(
      item =>
        jobs.products.every(({ product }) => product !== item.product) &&
        item.product.indexOf(jobs.productSearch) + 1
    )
  }
})(Products)
